<template>
  <div>
      <input class="search" placeholder="搜索歌名或歌手" type="text" @keyup.enter="searchHandle" v-model="search">
      <button @click="searchHandle" ><i class="el-icon-search"></i></button>
      <div class="title">
        <p>单曲</p>
        <p>歌手</p>
      </div>
      <div class="jieguo ">
        <ul class="left1">
          <router-link 
          tag="li"
          :to="{name:'Player',params:{id:item.id,name:item.name,image:encodeURIComponent(item.album.artist.img1v1Url)}}"
          v-for="(item,index) in searchData" 
          :key="index">{{item.name}}</router-link>
        </ul>
        <ul class="right1">
          <li v-for="(item,index) in searchSinger" 
          :key="index">{{item.name}}</li>
        </ul>
  </div>
  </div>
</template>

<script>
export default {
    name:'Search',
    data() {
      return {
        search:"",
        searchData:[],
        searchSinger:[]
      }
    },
    mounted(){
      
    },
    methods:{
      searchHandle(){
        this.$api.getSearch({
        keywords:this.search,
        limit:100
      }).then(res => {
        // console.log(res)
        this.searchData=res.result.songs
        
      })
        this.$api.getSearch({
          keywords:this.search,
          type:100,
          limit:100

        }).then(res => {
          // console.log(res)
          this.searchSinger=res.result.artists
        })
        
      }
    },
    
}
</script>

<style scoped>

.search{
  width: 100px;
  margin:8px 0 0 10px ;
  font-size: 13px;
}
button{
  border: none;
  background: rgba(255, 237, 237, 0.1);
  margin: 5px 0 0 1px;
}
i{
  font-size: 18px;
  color: #f78585;
  font-size: 18px;
}
.title{
  display: flex;
  margin-top: 10px;
  justify-content: space-around;
  color: #000000;
  background: rgba(250, 218, 218, 0.2);
}
.jieguo{
  display: flex;
  /* 元素两测留白 */
  justify-content: space-around;
  background: rgba(250, 218, 218, 0.2);
  
}
.jieguo ul li{
  text-align: center;
  width: 150px;
  margin: 5px 0;
  align-items:center;
  /* 取消换行 */
  white-space: nowrap;
  /* 隐藏溢出部分 */
  overflow: hidden;
  /* 超出用...表示 */
  text-overflow: ellipsis;
  color: rgb(179, 179, 179);
  font-size: 12px;
  
  
}


</style>